import React, {useRef, useState} from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCheck, faTrashAlt} from '@fortawesome/free-solid-svg-icons';
const Note = ({note, setNote}) => {

    const Delete = (e) =>
    {


        let close = e.target.closest(".Note");
        close.remove();
    }

    const Done = (e) =>
    {
        let close = e.target.closest(".Note");

        close.children[0].style.textDecoration = "line-through";
        close.style.marginRight = "100px";
        let close2 = e.target.closest(".done");
        close2.style.backgroundColor = "green";

    }
    return (
      <div className = "Note"  >

      <div className="container" >
          <br></br>
       <textarea  value={note.message} disabled ></textarea><br></br>
       
       </div>
       <div className="Buttongroup">
       <button className="done"onClick={Done}><FontAwesomeIcon icon={faCheck}/></button><button className="del"onClick={Delete}><FontAwesomeIcon icon={faTrashAlt}></FontAwesomeIcon></button>
       </div>
       
      </div>
    );
  }





export default Note;