import React, {useRef, useState} from 'react';
import Note from '../components/Note';
const NotesCreator = ({note, setNote}) => {

const msg = useRef("");

const AddNote = () =>
{
    if(msg.current.value != "")
    {
        setNote([...note, {message: msg.current.value, key: Math.random()}])
        msg.current.value = "";
    }

};

    return (
      <div className="NotesCreator">
          <div className="Logo">
              <br></br>
          <h1>My Todo List</h1>
 
          </div>
          <textarea  ref={msg}cols="10" rows="5"/><br></br>
          <button className="done" onClick={AddNote}>Add note</button>
          <br></br>
      </div>
    );
  }





export default NotesCreator;